<template>
  <div class="container-md">
    <LoadingSpinner :show="isLoading" />
    <div class="row">
      <div
        :style="{ backgroundImage: 'url(' + image.url + ')' }"
        class="img-banner m-0 p-0"
      ></div>
    </div>

    <div class="row mt-3">
      <div class="col-12 col-md-8 p-2">
        <div class="row">
          <div class="col-12">
            <h3>{{ title }}</h3>
          </div>

          <div class="col-12 mt-2">
            <div class="row">
              <div class="col-12 col-md-6">
                <ul>
                  <li>
                    Locale:

                    <span v-if="locale === 'online'" class="text-muted">
                      Online <i class="bi bi-camera-video-fill mx-1"></i
                    ></span>

                    <span v-else class="text-muted">
                      In person <i class="bi bi-people-fill mx-1"></i
                    ></span>
                  </li>
                  <li>
                    Audience: <span class="text-muted text-capitalize">{{ audience }}</span>
                  </li>
                  <small style="text-transform: none" class="text-muted"
                    >*{{ audienceNote }}</small
                  >
                </ul>
              </div>
              <div class="col-12 col-md-6">
                <ul class="">
                  <li>
                    Start: <span class="text-muted text-capitalize">{{ startDate }}</span>
                  </li>
                  <li>
                    End: <span class="text-muted text-capitalize">{{ endDate }}</span>
                  </li>
                </ul>
              </div>
            </div>
          </div>

          <div class="col-12 my-2">
            <h6 class="text-uppercase fw-bold">Description</h6>
          </div>
          <div class="col-12 col-md-8">
            <prismic-rich-text :field="description" />
          </div>
        </div>
      </div>
      <div class="col-12 col-md-4">
        <div class="row">
          <div class="card p-0">
            <div class="card-header">Price List:</div>
            <div class="card-body">
              <p class="card-text">
                It is only possible to participate in one or more module either
                online OR offline.
              </p>
              <p class="card-text">
                Full package: <span class="text-muted text-end">â‚¬{{price}}</span>
              </p>


<button v-if="isLoggedIn" class="snipcart-add-item btn btn-secondary"
  :data-item-id="id"
  :data-item-price="price"
  data-item-url="https://sfumato-v3.netlify.app/.netlify/functions/prismic"
  :data-item-image="image.url"
  :data-item-name="title">
  Add to cart
</button>


           
              <span v-else>Please signup or login to enroll!</span>
             
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      image: [],
      id: [],
      price: [],
      description: [],
      title: [],
      startDate: [],
      endDate: [],
      audience: [],
      locale: [],
      audienceNote: [],
      isLoading: false,
      msg: '',
    };
  },
  methods: {
    getContent(uid) {
      this.isLoading = true;
      //Query to get post content
      this.$prismic.client.getByUID("courses", uid).then((post) => {
        if (post) {
          this.image = post.data.course_image;
          this.description = post.data.course_description;
          this.title = post.data.course_title[0].text;
          this.id = post.id;
          this.price = post.data.price;
          let startDate = post.data.start_date;
          let dateStart = new Date(startDate + "T00:00:00");
          let options = {
            year: "numeric",
            month: "long",
            day: "numeric"
          };
          this.startDate = dateStart.toLocaleString("ru-UK", options);

          let endDate = post.data.end_date;

          let dateEnd = new Date(endDate + "T00:00:00");
        
          this.endDate = dateEnd.toLocaleString("ru-UK", options);

          this.audience = post.data.category;
          this.locale = post.data.online_or_offline;
          this.audienceNote = post.data.target_audience[0].text;
          this.isLoading = false;
        } else {
          //returns error page
          this.$router.push({ name: "not-found" });
        }
      });
    },
  },
  computed: {
    isLoggedIn() {
      return this.$store.getters.isAuthenticated;
    },
  },
  created() {
    this.getContent(this.$route.params.id);
  
  
  },
};
</script>
